<template>
    <div class="fenlei">
        <div class="f-left">
            <div
            v-for='(item, index) in FenleiChildren'
            :key="item.path"
            @click="getfenleiData(index)">
              <router-link
              :to='item.path'
              >
              {{item.LeftTitle}}
              </router-link>
            </div>
        </div>
        <div class="f-right">
            <div v-if='isList' class="f-fenlei">
              <div class="r-top">
                  <img :src=list.content.topImg>
              </div>
              <div class="lists"
              v-for='( list, key) in list.content.lists'
              :key="key"
              >
                  <div class="c-top"><span>{{list.stitle}}</span><span>{{list.more}}>></span></div>
                  <ul>
                      <li v-for='item in list.list'
                      :key='item.proId'
                      >
                      <router-link :to='`/catalog/${item.proId}`'>
                        <img v-lazy.container="item.img">
                        <a href = "javascript:void(0)">{{item.imgName}}</a>
                      </router-link>
                      </li>
                  </ul>
              </div>
            </div>
        </div>
    </div>
</template>

<script>
import { getFenlei } from '@/service.js'
import FenleiChildren from '@/router/fenleiChildren'
import '@/lib/fenlei.scss'
export default {
  name: 'fenlei',
  data () {
    return {
      msg: 'qwe',
      list: {},
      FenleiChildren,
      isList: false
    }
  },
  methods: {
    getfenleiData (index) {
      getFenlei().then(res => {
        if (res.data.code === '200') {
          this.list = res.data.data[index]
          this.isList = true
        }
      })
    }
  },
  mounted () {
    getFenlei().then(res => {
      if (res.data.code === '200') {
        this.list = res.data.data[0]
        this.isList = true
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.fenlei {
  display: flex;
  .f-left {
    width: 25%;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 24px;
    overflow: auto;
    a {
      display: block;
      height: 60px;
      line-height: 60px;
      text-align: center;
      border-bottom: 1px solid #e5e5e5;
      border-right: 1px solid #e5e5e5;
      color: #000000;
      .active {
        color: #00aa22;
        background-color: #f2f2f2;
      }
    }
  }
  .f-right {
    width: 72%;
  }
}
</style>



